.story-web {
    display: flex;
    align-items: center;
    justify-content: center;

    padding: 140px 0px 180px 0px;

    .container-web {
        display: flex;
        align-items: center;
        justify-content: center;

        margin-left: 160px;
        margin-right: 280px;

        .text-web {
            display: flex;
            flex-direction: column;

            margin-right: 100px;

            #title-web {
                color: #eee;
                font-family: "Roboto";
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                text-transform: uppercase;
            }

            #header-web {
                margin-top: 25px;

                color: #fff;
                font-family: "Roboto";
                font-size: 48px;
                font-style: normal;
                font-weight: 400;
            }

            #description-web {
                margin-top: 65px;

                color: #fff;
                font-family: "Roboto";
                font-size: 20px;
                font-style: normal;
                font-weight: 400;
            }
        }

        .creator-web {
            position: relative;

            #founder-web {
                width: 398px;
                height: 495.64px;

                border-radius: 10px;
                background: #d9d9d9;
            }

            .links-web {
                position: absolute;
                bottom: 30px;

                z-index: 20;
                width: calc(100% - 278px);

                margin-left: 156px;
                margin-right: 122px;

                display: flex;
                align-items: center;
                justify-content: space-between;

                img {
                    &:hover {
                        cursor: pointer;
                        filter: brightness(60%);
                    }
                }
            }

            .description-web {
                width: 134px;

                position: absolute;
                top: 80px;
                left: 350px;

                display: flex;
                flex-direction: column;
                align-items: flex-start;
                justify-content: center;

                padding: 13px 16px;
                margin: auto;

                border-radius: 5px 5px 5px 0px;
                background: #c3f53c;

                #name-web {
                    color: #000;
                    font-family: "Roboto";
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 500;
                }

                #title-web {
                    color: #000;
                    font-family: "Roboto";
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 400;
                }
            }
        }
    }
}
